import { gsap, SplitText } from 'vendors/gsap';

export default class HeroAnimation implements AnimationInterface {
  el?:HTMLElement;
  split?:SplitText;
  tl?:Timeline;
  total?: number;

  constructor(els:{
    el?:HTMLElement,
  }) {
    this.el = els.el;

    this.init();

    return this;
  }

  init() {
    if (this.el) {
      const title = this.el.querySelector('.title'); 
      const description = this.el.querySelector('.description'); 
      const cta = this.el.querySelector('.cta'); 
      const image = this.el.querySelector('.image'); 

      if (title) {
        this.split = new SplitText(title, {
          type: 'lines,words,chars',
          charsClass: 'char',
          linesClass: 'line',
          wordsClass: 'word',
          reduceWhiteSpace: false,
        });
      }

      if (!this.tl) {
        this.tl = gsap.timeline(
          { 
            id: 'hero',
            paused: true,
            autoRemoveChildren: false,
            repeatRefresh: true,
          }
        )
      }

      this.tl.add(
        gsap.fromTo(this.el, 
          { alpha: 0 }, 
          { alpha: 1, duration: 0.2, ease: 'power2.out', clearProps: 'all', overwrite: true}, 
        ),
        '0'
      )

      if (title && this.split) {
        this.tl.add(
          gsap.fromTo(this.split.words, 
            { alpha: 0, yPercent: 100 },
            { alpha: 1,  yPercent: 0, duration: 0.8,  stagger: { amount: 0.3 }, ease: 'power2.out', clearProps: 'alpha,yPercent', overwrite: true },
          ),
          '0.2'
        )
      }
      if (image) {
        this.tl.add(
          gsap.fromTo(image, 
            { alpha: 0, y: 20 },
            { alpha: 1, y: 0, duration: 0.6, ease: 'power2.out', clearProps: 'all', overwrite: true }
          ), 
          '0.4'
        )
      }

      if (description) {
        this.tl.add(
          gsap.fromTo(description, 
            { alpha: 0, y: 20 },
            { alpha: 1, y: 0, duration: 0.6, ease: 'power2.out', clearProps: 'all', overwrite: true }
          ), 
          '0.6'
        )
      }

      if (cta) {
        this.tl.add(
          gsap.fromTo(cta, 
            { alpha: 0, y: 20 },
            { alpha: 1, y: 0, delay: 0.5, duration: 0.6, ease: 'power2.out', clearProps: 'all', overwrite: true },
          ), 
          '0.6'
        )
      }
    }
  }

  enter = () => {
    if (this.tl) {
      this.tl.timeScale(1).play(0);
    }
  }

  display = () => {
    if (this.tl) {
      this.tl.totalProgress(1);
    }
  }

  update = () => {
    if (this.el) {
      const title = this.el.querySelector('.title'); 
      if (title) {
        this.split = new SplitText(title, {
          type: 'lines,words,chars',
          charsClass: 'char',
          linesClass: 'line',
          wordsClass: 'word',
          reduceWhiteSpace: false,
        });
      }
    }
  }

  leave = () => {
    if (this.tl) {
      this.tl.timeScale(2).reverse();
    }
  }

  dispose() {
    this.split?.revert();
    this.tl?.kill();
    this.split = undefined;
    this.tl = undefined;
  }
}
